@use '../mixins/buttons' as b;

.btn--primary {
    @include b.button('primary');
}
  
.btn--secondary {
    @include b.button('secondary');
}
  
.btn--outline {
    @include b.button('outline');
}
  
.btn--danger {
    @include b.button('danger', 'lg');
}
  
.btn--icon {
    @include b.button('primary', 'md', true);
}
  