@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200..1000;1,200..1000&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200..1000;1,200..1000&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
@font-face {
  font-family: "Nunito";
  font-style: normal;
  font-weight: 200 1000;
  src: url("https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;500;600;700;800;900&display=swap") format("woff2");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 200 1000;
  src: url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800;900&display=swap") format("woff2");
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  font-size: 62.5%; /* 1rem = 10px */
}

body {
  line-height: 1.5;
  font-family: sans-serif;
}

a {
  color: inherit;
  text-decoration: none;
}

ul li, ol li {
  list-style: none;
}

img, video {
  max-width: 100%;
  height: auto;
}

button {
  font-family: inherit;
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
}

input, textarea, select {
  font-family: inherit;
  border: none;
  outline: none;
}

/* ========================
   Light Theme (Default)
======================== */
:root {
  --brand-1: #98b59e;
  --brand-1-hover: #8ca68f;
  --brand-1-active: #7b9580;
  --brand-1-text: #ffffff;
  --brand-1-text-hover: #ffffff;
  --brand-2: #d47c52;
  --brand-2-hover: #c26f48;
  --brand-2-active: #a8613e;
  --brand-2-text: #ffffff;
  --brand-2-text-hover: #ffffff;
  --brand-3: #e8997a;
  --brand-3-hover: #d58868;
  --brand-3-active: #be7558;
  --brand-3-text: #ffffff;
  --brand-3-text-hover: #ffffff;
  --brand-4: #ffe0d8;
  --brand-4-hover: #ffd4cb;
  --brand-4-active: #ffc6bc;
  --brand-4-text: #404040;
  --brand-4-text-hover: #404040;
  --brand-5: #fbf8f4;
  --brand-5-hover: #f5dfca;
  --brand-5-active: #e0ecf0;
  --brand-5-text: #404040;
  --brand-5-text-hover: #404040;
  --brand-6: #f5dfca;
  --brand-6-hover: #fcdaba;
  --brand-6-active: #d0c4b8;
  --brand-6-text: #404040;
  --brand-6-text-hover: #404040;
  --brand-7: #e0ecf0;
  --brand-7-hover: #c9e7f1;
  --brand-7-active: #b0a8a0;
  --brand-7-text: #404040;
  --brand-7-text-hover: #404040;
  --brand-8: #404040;
  --brand-8-hover: #3a3a3a;
  --brand-8-active: #333333;
  --brand-8-text: #ffffff;
  --brand-8-text-hover: #ffffff;
  --white: #ffffff;
  --black: #000000;
}

/* ================================
   Dark Mode  Automatic by browser
================================= */
@media (prefers-color-scheme: dark) {
  :root {
    --brand-1: #456c59;
    --brand-1-hover: #3d5b4f;
    --brand-1-active: #334c43;
    --brand-1-text: #ffffff;
    --brand-2: #9d583c;
    --brand-2-hover: #874b32;
    --brand-2-active: #6f3d29;
    --brand-2-text: #ffffff;
    --brand-3: #c47763;
    --brand-3-hover: #aa6553;
    --brand-3-active: #925444;
    --brand-3-text: #ffffff;
    --brand-4: #ffcfc2;
    --brand-4-hover: #fcbeb0;
    --brand-4-active: #f7ac9b;
    --brand-4-text: #202020;
    --brand-5: #fbf8f4;
    --brand-5-hover: #f5dfca;
    --brand-5-active: #e0ecf0;
    --brand-5-text: #404040;
    --brand-6: #f5dfca;
    --brand-6-hover: #c0ad9c;
    --brand-6-active: #d0c4b8;
    --brand-6-text: #404040;
    --brand-7: #e0ecf0;
    --brand-7-hover: rgb(189, 182, 175);
    --brand-7-active: #b0a8a0;
    --brand-7-text: #404040;
    --brand-8: #ffffff;
    --brand-8-hover: #3a3a3a;
    --brand-8-active: #333333;
    --brand-8-text: #404040;
    --white: #ffffff;
    --black: #000000;
    --shadow: rgb(0 0 0 / 19%) 0px 0px 4px, rgb(0 0 0 / 20%) 0px 2px 4px;
    --shadow-inner:rgb(157, 167, 176) 3px 3px 6px 0px inset, rgba(255, 255, 255, 0.321) -3px -3px 6px 1px inset;
  }
}
