.container {
  width: 100%;
}
@media (min-width: 1024px) {
  .container {
    width: 85%;
    margin: auto;
  }
}

.no-display {
  display: none !important;
}

.no-cl ick {
  pointer-events: none;
}

.center {
  text-align: center;
  width: 100%;
}

.m-auto, .margin-auto {
  margin: auto;
}

.pointer {
  cursor: pointer;
}

.bold {
  font-weight: font(weight, bold);
}

.w-25 {
  width: 25%;
}

.w-50 {
  width: 50%;
}

.w-75 {
  width: 75%;
}

.w-100 {
  width: 100%;
}

.w-fit {
  width: fit-content;
}

.w-max {
  width: max-content;
}

.w-min {
  width: min-content;
}

.block {
  display: block;
  width: 100%;
}

.flex {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.flex-column {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.text-gradient {
  color: transparent !important;
  background: linear-gradient(90deg, var(--brand-2), var(--brand-1));
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 100%;
  width: fit-content;
}

.only--mobile {
  display: block;
}
@media (min-width: 795px) {
  .only--mobile {
    display: none;
  }
}

.only--tablet {
  display: block;
}
@media (min-width: 1024px) {
  .only--tablet {
    display: none;
  }
}

.only--desktop {
  display: none !important;
}
@media (min-width: 1024px) {
  .only--desktop {
    display: block;
  }
}

.show {
  display: block !important;
}

.hide {
  display: none !important;
}

.link {
  text-decoration: none;
  color: var(--brand-3);
  font-weight: font(weight, bold);
}
.link:hover {
  color: var(--brand-1);
}

.top-page {
  background-color: var(--brand-3);
  position: fixed;
  color: var(--brand-3-text);
  padding: 1rem 1.5rem;
  font-size: 3rem;
  border-radius: 50%;
  bottom: 4%;
  right: 4%;
  z-index: 1000;
  cursor: pointer;
}
.top-page:hover {
  animation: top-page 0.5s infinite alternate cubic-bezier(1, 0, 0, 1);
}
@keyframes top-page {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-0.5rem);
  }
}

.no-list-style {
  list-style: none;
}
.no-list-style li:not(:last-child) {
  margin-bottom: 1.5rem;
}
.no-list-style li:not(:last-child)::after {
  content: "";
  display: block;
  width: 100%;
  height: 0.2rem;
  background-color: var(--brand-5-active);
  margin-top: 1.5rem;
}

