.alert {
  padding: 1rem 2rem;
  border-radius: 0.5rem;
  font-weight: bold;
  margin-bottom: 1rem;
}
.alert--error {
  background-color: #ffd2d2;
  color: #b30000;
}
.alert--success {
  background-color: #d4edda;
  color: #155724;
}

.btn--primary {
  background-color: var(--brand-1);
  color: var(--brand-1-text);
  padding: 1rem 2rem;
  font-size: 1.4rem;
  font-weight: bold;
  border-radius: 1rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}
.btn--primary:hover {
  background-color: var(--brand-1-hover);
  transform: scale(1.03);
}
@media (max-width: 794px) {
  .btn--primary {
    width: 100%;
